      formId: tableName,
      currentRow: null,
      isIn: true,
      tableDataOut: [], // 境外人员的表格信息
      personCollection: {
        porsonStatus: '正常',
        cardType: '身份证',
        cardNumber: '',
        personName: '',
        sitizenship: '中国',
        sex: '',
        education: '',
        bothday: '',
        taxPlayerNumber: '',
        personStatus: [],
        disabilityNumber: '',
        martyrsFamily: '',
        remark: '',
        employmentType: '',
        employmentStartTime: '',
        employmentEndTime: '',
        employmentNumber: '',
        employmentPosition: '',
        telNumber: '',
        email: '',
        alwaysLivePlaceTitle: '',
        alwaysLivePlaceDetail: '',
        callPlaceTitle: '',
        callPlaceDetail: '',
        bankName: '',
        bankCardNumber: '',
        houserPlaceTitle: '',
        houserPlaceDetail: '',
        personalInvestment: '',
        personalInvestmentPercent: '',
        isBaosong: '未报送',
        isShenfen: '未验证'
      },
      selectCollection: {
        texPlayerNumber: '',
        employmentNumber: '',
        personName: '',
        cardNumber: '',
        isShenfen: '',
        isBaosong: '',
        porsonStatus: '',
        isCangulie: '',
        employmentType: ''
      },
      rules: {
        cardType: [
          { required: true, message: '请填选择证件类型', trigger: 'change' }
        ],
        cardNumber: [
          { required: true, message: '请填写身份证号码', trigger: 'change' }
        ],
        personName: [
          { required: true, message: '请填写姓名', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '请选择性别', trigger: 'change' }
        ],
        bothday: [
          { required: true, message: '请选择出生日期', trigger: 'change' }
        ],
        employmentType: [
          { required: true, message: '请选择从业类型', trigger: 'change' }
        ],
        employmentNumber: [
          { required: true, message: '请填写工号', trigger: 'change' }
        ]
      },
      personCollectionTableHead: []
    }
  },
  computed: {
    ...mapState(['number']),
    ...mapGetters(['company']),
    tablePersonCollection () {
      return this.$store.state.form.personCollection
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
  },
  watch: {
    centerDialogVisible (val) {
      if (!val) {
        this.isUpdata = false
        this[tableName] = this.$options.data()[tableName]
      }
    }
  },
  methods: {
    ...formHelper,
    inClick () {
      this.isIn = true
    },
    outClick () {
      this.isIn = false
    },
    addFormClick (formId) {
      if (!this.isIn) {
        this.$message.warning('暂时只支持添加境内人员功能')
        return false
      }
      if (this.$refs[formId]) {
        this.$refs[formId].resetFields()
      }
      this.centerDialogVisible = true
    },
    submitForm (formName) { // 提交表单的操作
      let _this = this
      this.$refs[formName].validate((valid) => { // 提交的操作
        if (valid) { // 如果验证通过可以提交
          let submitData = {} // 用于提交的主键，格式参考store
          let personCollectionForm = JSON.parse(JSON.stringify(_this.personCollection))
          if (!personCollectionForm.infoId) {
            personCollectionForm.infoId = _this.$store.getters.getFormInfoId('personCollection')
          }
          personCollectionForm.isCanji = '否'
          personCollectionForm.isLieshu = '否'
          personCollectionForm.isGulao = '否'
          for (let i in personCollectionForm.personStatus) {
            if (personCollectionForm.personStatus[i] === '残疾') {
              personCollectionForm.isCanji = '是'
              continue
            }
            if (personCollectionForm.personStatus[i] === '烈属') {
              personCollectionForm.isLieshu = '是'
              continue
            }
            if (personCollectionForm.personStatus[i] === '孤老') {
              personCollectionForm.isGulao = '是'
            }
          }
          submitData.formId = formName
          submitData.content = personCollectionForm
          submitData.key = 'cardNumber'
          if (_this.isUpdata) {
            _this.$store.commit('updataFormInfo', submitData)
          } else {
            _this.$store.commit('addFormInfo', submitData)
          }
          this.$refs['personCollectionTable'].clearSelection()
          _this.centerDialogVisible = false
        }
      })
    },
    huoqufankui () {
      this.$store.commit('numAdd')
    },
    tableRowClassName ({ row, rowIndex }) {
      return ''
    },
    shenfenChange () {
      this.personCollection.bothday = dataHelper.getBirth(this.personCollection.cardNumber)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    margin: 0;
  }

  a {
    color: #42b983;
  }

  img {
    vertical-align: middle;
  }

  .collection {
    font-size: 12px;
    text-align: start;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    border-bottom: none;
    padding: 0 10px;
    margin: 0 35px 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 0 0 20px;
  }

  .collection {
    overflow: hidden;
  }

  .select {
    background-color: rgb(249, 250, 253);
    padding-top: 25px;
  }
</style>
<style>
  .el-table__row.orange {
    background-color: #5a98de;
  }

  .el-button + .el-button {
    margin: 0;
  }

  .el-dialog__body {
    padding: 0;
  }

  .person-status .el-form-item__label {
    position: relative;
    float: right;
    right: 180px;
  }

  .person-status.el-form-item {
    margin-bottom: 5px;
  }

  [data-v-130d4e25] .el-radio-group {
    float: right;
    position: relative;
    right: -140px;
  }

  .el-form-item {
    margin-bottom: 18px;
  }

  [data-v-130d4e25] .el-radio__input {
    display: none;
  }

  [data-v-130d4e25] .el-radio.el-radio--medium {
    margin-right: 10px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }
</style>



